import React from 'react';
import { createStyles, makeStyles } from '@material-ui/core/styles';

type StyleProps = {
  color: string,
  backgroundColor: string,
}

const useStyles = makeStyles(() =>
  createStyles({
    circle: (props: StyleProps) => ({
      width: '46px',
      height: '46px',
      borderRadius: '50%',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      fontSize: '15px',
      fontWeight: 700,
      border: '4px solid #fff',
      color: props.color,
      backgroundColor: props.backgroundColor,
      zIndex: 1,
      '& + div': {
        margin: '0 -10px',
        zIndex: 0,
      },
    }),
  }),
);

type Props = {
  color: string,
  backgroundColor: string,
}

export function Circle(props: React.PropsWithChildren<Props>): React.ReactElement {
  const classes = useStyles(props);
  return (
    <div className={classes.circle}>{props.children}</div>
  );
}

export default Circle;
