import { createMuiTheme } from '@material-ui/core';
import createBreakpoints from '@material-ui/core/styles/createBreakpoints';

const breakpointValues = {
  xs: 0,
  sm: 600,
  md: 960,
  lg: 1280,
  xl: 1920,
};
const breakpoints = createBreakpoints({ values: breakpointValues });

const theme = createMuiTheme({
  breakpoints,
  palette: {
    primary: {
      main: '#101010',
    },
    background: {
      default: '#F8F5F5',
    },
  },
  typography: {
    fontFamily: 'DM Sans, sans-serif',
  },
  overrides: {
    MuiToolbar: {
      root: {
        justifyContent: 'space-between',
      },
      regular: {
        height: '60px',
        [breakpoints.up('xs')]: {
          minHeight: '60px',
        },
        [breakpoints.down('xs')]: {
          minHeight: '48px',
          height: '48px',
        },
      },
    },
    MuiTabs: {
      root: {
        backgroundColor: '#fff',
      },
      indicator: {
        display: 'none',
      },
    },
    MuiTypography: {
      h1: {
        fontSize: '2rem',
        fontWeight: 700,
      },
    },
  },
});

export default theme;
