import React, { useState } from 'react';
import clsx from 'clsx';
import { Badge, Button, Menu, MenuItem, withStyles } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import DropdownIcon from '@material-ui/icons/ExpandMore';

const useStyles = makeStyles((theme) => ({
  button: {
    textTransform: 'none',
    width: '220px',
    padding: '6px 16px',
  },
  text: {
    color: '#84868A',
  },
  flex: {
    display: 'flex',
  },
  spaceBetween: {
    justifyContent: 'space-between',
  },
  badge: {
    color: 'green'
  },
}));

const StyledMenu = withStyles((theme) => ({
  paper: {
    width: '220px',
    backgroundColor: '#0C172E',
    color: '#fff',
    borderRadius: '10px',
  },
  list: {
    padding: 0,
  },
}))(Menu);

const StyledBadge = withStyles((theme) => ({
  root: {
    margin: '0 18px 0 4px',
  },
  colorPrimary: {
    backgroundColor: '#5D6677',
  },
  colorSecondary: {
    backgroundColor: '#FF9900',
  },
}))(Badge);

type Props = {
  label: string,
  choices: string[],
}

export function Filter(props: Props): React.ReactElement {
  const classes = useStyles();
  const [selectedLabel, setLabel] = useState('');
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  const handleChange = (event: React.MouseEvent<HTMLLIElement>) => {
    setLabel(event.currentTarget.innerText);
    setAnchorEl(null);
  };
  return (
    <>
      <Button onClick={handleClick} className={clsx(classes.button, classes.flex, classes.spaceBetween)}>
        <span className={classes.text}>{props.label}:</span>
        <span className={classes.flex}>
          {selectedLabel}
          <DropdownIcon />
        </span>
      </Button>
      <StyledMenu
        variant="menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <MenuItem onClick={handleClose} className={clsx(classes.flex, classes.spaceBetween)}>
          {props.label}:
          <DropdownIcon />
        </MenuItem>
        {props.choices.map(label => (
          <MenuItem onClick={handleChange}>
            <StyledBadge
              color={label === selectedLabel ? 'secondary' : 'primary'}
              variant="dot"
            />
            {label}
          </MenuItem>
        ))}
      </StyledMenu>
    </>
  );
}

export default Filter;
